body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --header-font-header4-medium: "PingFang SC";
  --font-inherit: inherit;

  /* font sizes */
  --header-font-header4-medium-size: 20px;
  --header-font-header3-medium-size: 24px;
  --header-font-header1-medium-size: 38px;

  /* Colors */
  --bw-white: #fff;
  --color-aliceblue: #f6fbff;
  --primary-color-primary-default: #1e76f0;
  --color-gray: #202241;
  --text-gray-color-text1-3: #747c94;

  /* Gaps */
  --gap-xs: 12px;

  /* Paddings */
  --padding-5xl: 24px;
  --padding-29xl: 48px;
  --padding-base: 16px;
  --padding-8xl: 27px;

  /* border radiuses */
  --br-xs: 12px;

  /* Effects */
  --border-inside-bottom: 0px -1px 0px #e6e6e6 inset;
}
